import { Component } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { SearchService } from './search.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  searchCpfCnpj: FormGroup;
  cpf: any;

  constructor(
    public fb: FormBuilder,
    public searchService: SearchService
  ) {
    this.searchCpfCnpj = fb.group({
      cpfcnpj: ['', [Validators.required, Validators.minLength(8)]]
    });
  }

  search(form: any) {
    this.getCPFOrCNPJ(form.cpfcnpj);
  }

  getCPFOrCNPJ(cpfcnpj: string) {
    try {
      this.searchService.getCPFOrCNPJ(cpfcnpj).subscribe((response: any) => {
        if (!response || response.status === false) {
          throw new Error('data empty');
        }

        this.cpf = response.data;
        console.log(this.cpf);
      }, (error) => {
        throw new Error(error);
      });
    } catch (error) {
      console.log('[ERROR 001] ', error);
    }
  }

  get cpfcnpj() { return this.searchCpfCnpj.get('cpfcnpj'); }
}
